import React, { useEffect, useState } from 'react';
import { FaLinkedinIn, FaGithubAlt, FaInstagram } from 'react-icons/fa';

import api from '~/services/api';
import { Container, NameContainer, IconContainer } from './styles';

export default function Header() {
  const [avatar, setAvatar] = useState('');
  useEffect(() => {
    async function loadAvatar() {
      const { data } = await api.get('/users/igorjung');

      setAvatar(data.avatar_url);
    }

    loadAvatar();
  }, []);

  return (
    <Container>
      <NameContainer>
        <img src={avatar} alt="igorjung" />
        <strong>Igor Jung</strong>
      </NameContainer>

      <nav>
        <IconContainer
          target="_blank"
          href="https://www.instagram.com/igorjung0/"
        >
          <FaInstagram size={20} color="#eee" />
        </IconContainer>
        <IconContainer target="_blank" href="https://github.com/igorjung">
          <FaGithubAlt size={20} color="#eee" />
        </IconContainer>
        <IconContainer
          target="_blank"
          href="https://www.linkedin.com/in/igor-c%C3%A1ssio-jung-005880169/"
        >
          <FaLinkedinIn size={20} color="#eee" />
        </IconContainer>
      </nav>
    </Container>
  );
}
